<template>
    <div>
        <Mheader>列表页</Mheader>
        <div class="content" ref="scroll" @scroll="loadMore">
            <ul>
                <router-link :to="{name:'detail',params:{bid:book.bookId}}" v-for="(book,index) in books" :key=index tag="li">
                    <img v-lazy="book.bookCover" alt="">
                    <div>
                        <h4>{{book.bookName}}</h4>                    
                    <p>{{book.bookInfo}}</p> 
                    <b>{{book.bookPrice}}</b>
                    <div class="btn-list">
                      <button class="delete" @click.stop="remove(book.bookId)">删除</button>
                      <button class="add">添加</button>
                      </div>
                    </div>
                </router-link>
            </ul>
            <div @click="more" class="more">加载更多</div>
        </div>
    </div>
</template>
<script>
import Mheader from "../base/Mheader.vue";
import { pagination, removeBook } from "../api";
export default {
  created() {
    this.getData();
  },
  mounted() {
    /* this.$refs.scroll.addEventListener("scroll", () => {
      console.log(1);
    }); */
    let scroll = this.$refs.scroll; //获取要拖曳的元素
    let top = scroll.offsetTop;
    let distance = 0;
    let isMove = false;
    scroll.addEventListener(
      "touchstart",
      e => {
        //滚动条在最顶端时，或者当前盒子在顶端得时候，才继续走
        if (scroll.scrollTop !== 0 || scroll.offsetTop !== top) return;
        let start = e.touches[0].pageY; //手指滑动的开始
        let move = e => {
          isMove = true;
          let current = e.touches[0].pageY;
          distance = current - start; //求得拉动的距离，负的就不要了
          if (distance > 0) {
            if (distance <= 80) {
              scroll.style.top = distance + top + "px";
            } else {
              //如果大于80了，认为就是80px
              distance = 80;
              scroll.style.top = top + distance + "px";
            }
          } else {
            scroll.removeEventListener("touchmove", move);
            scroll.removeEventListener("touchend", end);
          }
        };
        let end = e => {
          if(!isMove)return;
          isMove = false;
          clearTimeout(this.timer1);
          this.timer1 = setInterval(() => {
            if (distance <= 0) {
              clearTimeout(this.timer1);
              distance = 0;
              scroll.style.top = 40 + "px";
              scroll.removeEventListener("touchmove", move);
              scroll.removeEventListener("touchend", end);
              this.offset = 0;
              this.books = []; //先清空数据
              this.getData();
              return;
            }
            //一共distance这么高，每次-1
            distance -= 1;
            scroll.style.top = top + distance + "px";
          }, 1);
        };
        scroll.addEventListener("touchmove", move);
        scroll.addEventListener("touchend", end);
      },
      false
    );
  },
  components: {
    Mheader
  },
  data() {
    //offset代表偏移量，hasMore是否有更多
    return { books: [], offset: 0, hasMore: true, isLoading: false }; //默认不是正在加载
  },
  methods: {
    async remove(id) {
      await removeBook(id); //删除某一项
      //要删除前台数据
      this.books = this.books.filter(item => item.bookId !== id);
    },
    async getData() {
      if (this.hasMore && !this.isLoading) {
        //有更多的时候获取数据
        this.isLoading = true;
        let { hasMore, books } = await pagination(this.offset);
        this.books = [...this.books, ...books]; //获取的书放到books属性上
        this.hasMore = hasMore;
        this.isLoading = false; //加载完毕
        this.offset = this.books.length; //维护偏移量 就是总书的长度
      }
    },
    more() {
      this.getData();
    },
    loadMore() {
      //卷去的高度，当前可见区域，总高
      //触发scroll事件，可能触发n次，先进来开一个定时器，下次触发时，将上一次的定时器清除掉
      clearTimeout(this.timer); //节流防抖
      this.timer = setTimeout(() => {
        let { scrollTop, clientHeight, scrollHeight } = this.$refs.scroll;
        if (scrollTop + clientHeight + 20 > scrollHeight) {
          this.getData();
        }
      }, 60);
    }
  }
};
</script>
<style scoped>
.content ul {
  padding: 10px;
}
.content ul li {
  display: flex;
  padding-bottom: 10px;
  border-bottom: 1px solid #f1f1f1;
}
.content ul li img {
  width: 130px;
  height: 150px;
}
.content h4 {
  font-size: 20px;
  line-height: 25px;
}
.content p {
  color: #2a2a2a;
  line-height: 25px;
}
.content b {
  color: red;
}
.content button {
  display: block;
  width: 60px;
  height: 30px;
  color: #fff;
  border: none;
  border-radius: 15px;
  outline: none;
}
.delete{  
  background: orangered;
}
.add{ 
  background: skyblue;
}
.more {
  margin: 10px;
  background: #2afedd;
  height: 30px;
  line-height: 30px;
  text-align: center;
  font-size: 20px;
}
.btn-list{
  display: flex;
  justify-content: space-around;
}
</style>


